import { $Q } from "../utils/query-selector";

if (sessionStorage.getItem('register-attempt')) {
  const form = $Q('.shopify-challenge__container form');

  const inputRedirect = '<input type="hidden" name="return_to" value="/account">';
  form.insertAdjacentHTML('afterbegin', inputRedirect);
  sessionStorage.removeItem('register-attempt');
}
